/* App.css */

/* 전체 페이지 스타일 */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
}

/* 헤더 스타일 */
.App-header {
  background-color: #343a40;
  color: white;
  padding: 20px 0;
  text-align: center;
}

.App-header h1 {
  margin: 0;
  font-size: 2rem;
}

/* 메인 컨텐츠 스타일 */
main {
  padding: 20px;
}

/* 날씨 정보 박스 스타일 */
.weather-box {
  background-color: #e9ecef;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
}

.weather-box h2 {
  margin-top: 0;
  font-size: 1.5rem;
}

.weather-box p {
  margin-top: 0;
}

/* 추천 음식 박스 스타일 */
.food-box {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
}

.food-box h2 {
  margin-top: 0;
  font-size: 1.5rem;
}

.food-box p {
  margin-top: 0;
}

/* 푸터 스타일 */
footer {
  background-color: #343a40;
  color: white;
  text-align: center;
  padding: 20px 0;
}

/* 감귤, 한라봉 이미지 스타일 */
.fruits-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.fruits-container img {
  width: 100px;
  height: 100px;
  margin: 0 10px;
  border-radius: 50%;
  border: 2px solid #343a40;
}

/* 추천 음식 리스트 스타일 */
.food-list {
  list-style-type: none;
  padding: 0;
}

.food-list li {
  font-size: 1.2rem;
  margin-bottom: 10px;
}
